import React, { useEffect, useState } from "react"
import {Row, Col, Typography } from 'antd'
import ManagementBase from "../../Components/Management/Base"
import { getAllUsers, getAuthToken, getUser } from "../../Helper/Authentication"
import { getMessages } from "../../Helper/Message"
import { getCourses } from "../../Helper/Course"
import { getGroups } from "../../Helper/Group"
import { getPolicies } from "../../Helper/Policy"
import { getPosts } from "../../Helper/Post"
import { getAnnouncements } from "../../Helper/Announcement"








const ManagementDashboard = () => {
    const [user] = useState(getAuthToken())

    useEffect(() => {
        document.title = "Dashboard | SoCIS"
        for(let i = 0; i < 100; i++){
            getUser()
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
        for(let i = 0; i < 100; i++){
            getAllUsers()
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
        for(let i = 0; i < 100; i++){
            getMessages()
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
        for(let i = 0; i < 100; i++){
            getAnnouncements({ page: 1 })
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
        for(let i = 0; i < 100; i++){
            getCourses()
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
        for(let i = 0; i < 100; i++){
            getPolicies()
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
        for(let i = 0; i < 100; i++){
            getPosts(1)
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
        for(let i = 0; i < 100; i++){
            getGroups()
                .then(user => console.log(user))
                .catch(error => console.log(error))
        }
    })
    return(
       <ManagementBase>
        <Typography.Title>
            Greetings, <br/>{user.user.firstName}
        </Typography.Title>
        <Row>
                <Col
                    xs={{
                        span: 5,
                        offset: 1,
                    }}
                    lg={{
                        span: 6,
                        offset: 2,
                    }}
                >
                    
                </Col>
        </Row>
       </ManagementBase>
    )
}

export default ManagementDashboard